import { ref, onMounted } from 'vue';
import logoImgLight from "@/assets/newHome/logo/Full_Logo_Vertical_Transparent_Light.png";
import logoImgDark from "@/assets/newHome/logo/Full_Logo_Vertical_Transparent_Dark.png";
import logoImgDarkH from '@/assets/newHome/logo/Full_Logo_Horizontal_Transparent_Dark.png';
export default {
  name: "homeDonghua",

  setup() {
    const visible = ref(true);
    const span1 = ref(false);
    const span2 = ref(false);
    const span3 = ref(false);
    const span4 = ref(false);
    const show1 = ref(false);
    const show2 = ref(false);
    const show3 = ref(false);
    const show4 = ref(false);
    const show5 = ref(false);
    const cl = ref(false);
    const cr = ref(false);

    const skip = () => {
      let body = document.body;
      let documentTop = document.scrollingElement.scrollTop;
      visible.value = false;
      localStorage.setItem('homeDongHua', 1);
      body.style.position = "static";
      body.style.top = "auto";
      document.scrollingElement.scrollTop = documentTop;
    };

    onMounted(() => {
      let homeDongHuaValue = localStorage.getItem('homeDongHua');

      if (homeDongHuaValue) {
        visible.value = false;
      } else {
        let body = document.body;
        let documentTop = document.scrollingElement.scrollTop;
        body.style.position = "fixed";
        body.style.top = -documentTop + "px";
        setTimeout(function () {
          span1.value = true;
        }, 500);
        setTimeout(function () {
          span2.value = true;
        }, 1200);
        setTimeout(function () {
          span3.value = true;
        }, 1700);
        setTimeout(function () {
          span2.value = false;
          span4.value = true;
        }, 21200);
        setTimeout(function () {
          show1.value = true;
        }, 2200);
        setTimeout(function () {
          show1.value = false;
          show2.value = true;
        }, 6200);
        setTimeout(function () {
          // show1.value = false
          show2.value = false;
          show3.value = true;
        }, 10200);
        setTimeout(function () {
          // show1.value = false
          // show2.value = false
          show3.value = false;
          show4.value = true;
        }, 14200);
        setTimeout(function () {
          // show1.value = false
          // show2.value = false
          // show3.value = false
          show4.value = false;
          show5.value = true;
        }, 18200);
        setTimeout(function () {
          cl.value = true;
          cr.value = true;
        }, 26200);
        setTimeout(function () {
          visible.value = false;
          localStorage.setItem('homeDongHua', 1);
          body.style.position = "static";
          body.style.top = "auto";
          document.scrollingElement.scrollTop = documentTop;
        }, 28200);
      }
    });
    return {
      span1,
      span2,
      span3,
      span4,
      show1,
      show2,
      show3,
      show4,
      show5,
      cl,
      cr,
      visible,
      skip
    };
  },

  data() {
    return {
      logoImgLight,
      logoImgDark,
      logoImgDarkH
    };
  },

  methods: {
    turnHome() {
      this.$router.push('/');
    }

  }
};